<template>
  <v-container style="height: 100%; margin-top: 50px; margin-left: -15px;">
    <v-infinite-scroll :height="pageHeight" :width="pageWidth" @load="load">
      <template v-slot:loading>
        <v-progress-circular color="teal" indeterminate size="30" :width="2">
        </v-progress-circular>
        <h3 class="mx-2" style="color: teal;">Cargando más productos...</h3>
      </template>
      <div style="display: flex; flex-wrap: wrap;gap: 20px;">
        <template v-for="(item, index) in items" :key="index">
          <product-card v-bind:product_name="item.name" v-bind:product_img="item.image"></product-card>
        </template>
      </div>
    </v-infinite-scroll>
  </v-container>
</template>
<script>
import ProductCard from '@/components/Store/ProductCard.vue';
import { mapState, mapActions } from "vuex";
export default {
  name: "Store",
  components: { ProductCard },
  data: () => ({
    items: []
  }),
  mounted() {
    this.loadAllProducts();
  },
  computed: {
    ...mapState("layout", ['general_loading']),
    pageHeight() {
      return window.innerHeight - 165;
    },
    pageWidth() {
      return window.innerWidth;
    },
  },
  methods: {
    ...mapActions("layout", ["showGeneralLoading"]),
    createRange(length, start) {
      return Array.from({ length }).map((_, i) => i + start)
    },
    load({ done }) {
      setTimeout(() => {
        this.items.push(...Array.from({ length: 10 }, (k, v) => v + this.items.at(-1) + 1))
        this.items = this.items.map(function sumaUno() {
        let product = {
          name: 'Example Product Name',
          image: '../img/products/ipad.png'
        }
        return product;
      });
        done('ok')
      }, 1000)
    },
    loadAllProducts() {
      this.showGeneralLoading(true);
      this.items = Array.from({ length: 50 }, (k, v) => v + 1);
      this.items = this.items.map(function sumaUno() {
        let product = {
          name: 'Example Product Name',
          image: '../img/products/ipad.png'
        }
        return product;
      });
      this.showGeneralLoading(false);
    }
  },
}
</script>
<style scoped>
.card_product_style {
  width: 200px;
  height: 300px;
}
</style>