
const SHOW_GENERAL_LOADING = "SHOW_GENERAL_LOADING";
const SET_FIRST_LOADING = "SET_FIRST_LOADING";
const state = {
  general_loading:false,
  first_loading:true
};
const mutations={
  SHOW_GENERAL_LOADING(state, loading) {
    state.general_loading = loading;
  },
  SET_FIRST_LOADING(state, set_first) {
    state.first_loading = set_first;
  },
};
const actions={
  showGeneralLoading({ commit }, loading) {
    commit(SHOW_GENERAL_LOADING, loading);
  },
  setFirstLoading({ commit }, set_first) {
    commit(SET_FIRST_LOADING, set_first);
  },
};
export default {
  namespaced: true,
  state,
  mutations,
  actions,
};
