<template>
  <v-navigation-drawer :v-model="show_hidde_menu_nav" temporary absolute location="left" class="local_nav_style_class">
    <v-list>
     <!-- <v-list-item prepend-avatar="https://randomuser.me/api/portraits/men/85.jpg" title="Roider Rojas Sanchez" -->
      <v-list-item prepend-avatar="../img/roy_avatar.png" title="Roider Rojas Sanchez"
        subtitle="rrojass900823@gmail.com" nav>
        <template v-slot:append>
          <v-btn size="small" variant="text" icon="mdi mdi-cog-outline" @click="goToProfileView"></v-btn>
        </template>
      </v-list-item>
    </v-list>
    <v-divider></v-divider>
    <v-list :lines="false" density="compact" nav>
      <v-list-item v-for="(item, i) in items" :key="i" :value="item" color="teal" @click="goToPageNav(item)">
        <template v-slot:prepend>
          <v-icon :icon="item.icon"></v-icon>
        </template>
        <v-list-item-title color="teal" v-text="item.text"></v-list-item-title>
      </v-list-item>
    </v-list>
    <template v-slot:append>
      <div class="pa-2">
        <v-btn block variant="flat" color="red-lighten-2" class="text-none text-white" prepend-icon="mdi-logout">
          Salir
        </v-btn>
      </div>
    </template>
  </v-navigation-drawer>
</template>

<script>
import { appRoutes } from '@/constants';
export default {
  name: "MenuBarNav",
  watch: {},
  computed: {},
  data() {
    return {
      items: [
        { text: 'Inicio', goto: "home", icon: 'mdi-home' },
        { text: 'Tienda', goto: "store", icon: 'mdi-storefront-outline' },
        { text: 'Recientes', goto: "dashboard", icon: 'mdi-history' },
        { text: 'Principales', goto: "dashboard", icon: 'mdi-star' },
        { text: 'Descargas', goto: "dashboard", icon: 'mdi-download' },
        { text: 'Salvados', goto: "dashboard", icon: 'mdi-cloud-upload' },
      ],
    }
  },
  props: {
    show_hidde_menu_nav: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    goToProfileView() {
      this.navigateTo(appRoutes.profile);
    },
    goToPageNav(item) {
      this.navigateTo(item.goto);
    }
  },
}
</script>
<style scope lang="css">
.local_nav_style_class {
  padding: 0 !important;
  width: 300px;
}
</style>
