<template>
  <v-dialog :v-model="show_terminos_condiciones" persistent width="auto">
    <v-card>
      <v-card-text>
        Los términos y condiciones de uso (también conocidos cómo términos y condiciones) son un contrato legal entre el
        proveedor de un servicio y la persona que quiere usar dicho servicio. La persona debe aceptar cumplir estos
        términos para usar el servicio ofertado. Los términos de uso pueden ser también para descargar la responsabilidad,
        especialmente en el uso de sitios web. Normalmente el lenguaje vago y las oraciones largas y complejas usadas en
        los términos de uso han traído preocupaciones acerca de la privacidad del cliente y ha hecho que aumente la
        conciencia pública con respecto a este tema.
        El contrato de términos de uso tiene un propósito legal y es usado por compañías que dan servicios de software,
        como buscadores web, comercio online y redes sociales. Un acuerdo de términos de servicio legítimo es legalmente
        vinculante y puede estar sujeto a cambios. Las compañías pueden hacer cumplir los términos negando el servicio.
        Los clientes pueden hacerlos cumplir presentando una demanda en el caso de que puedan demostrar que realmente se
        vieron perjudicados por el incumplimiento de los términos.
        En 2017, la artista Joana Moll compró por 136€ un millón de perfiles de una app de citas norteamericana que opera
        a nivel global. Estos perfiles incluían fotos, nombres de usuarios, direcciones de email, nacionalidades, géneros,
        edades e información personal tales como orientación sexual, intereses, profesión y rasgos físicos y de
        personalidad. Con estos perfiles, Joana Moll creó un proyecto, “The Dating Brokers”2​, con la intención de exponer
        a las compañías que están comerciando con la información personal de los usuarios sin el consentimiento consciente
        de los mismos. Este proyecto simula una página web de subastas de perfiles.

        “Terms of Service; Didn’t Read”3​, es un grupo que evalúa los términos de uso de algunos servicios digitales.
        Puntúa una serie de cláusulas de manera individual en función de si cumplen el RGPD, junto con una pequeña
        descripción. A partir de esa puntuación se le asigna una categoría a cada servicio en función de cómo cumple el
        reglamento. De esta forma los usuarios pueden acceder a una información resumida sin necesidad de leer los
        términos, que son generalmente largos.
      </v-card-text>
      <v-card-actions>
        <v-checkbox class="float-right" v-model="aggree_chbox" label="Acepto los Terminos y Condiciones" color="primary"
          value="false" hide-details></v-checkbox>
        <v-btn class="text-none ms-4 text-white" :disabled="disaggree_disabled" color="red-darken-1" variant="flat"
          @click="ActionTyC('no_acepto')">
          No Acepto
        </v-btn>
        <v-btn class="text-none ms-4 text-white" :disabled="aggree_disabled" color="teal" variant="flat"
          @click="ActionTyC('acepto')">
          Continuar
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'TerminosCondiciones',
  props: {
    show_terminos_condiciones: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      aggree_disabled: true,
      disaggree_disabled: false,
      aggree_chbox: false
    }
  },
  watch: {
    aggree_chbox(e) {
      if (e === false) {
        this.aggree_disabled = true
        this.disaggree_disabled = false
      }
      else {
        this.aggree_disabled = false
        this.disaggree_disabled = true
      }
    }
  },
  methods: {
    ActionTyC(action) {
      this.$emit("ActionInTyC", action)
    },
  }
}
</script>
<style scoped></style>
