<template>
  <v-app>
    <default-bar v-on:clickOnLoginAction="showLogin" v-on:showHiddeMenuAction="showHiddeMenu" />
    <MenuBarNav v-model="show_hidde_menu_nav" />
    <!-- <terminos-condiciones v-model="show_terminos_condiciones" v-on:ActionInTyC="ActionInTyC"></terminos-condiciones> -->
    <!-- <PrincipalView></PrincipalView> -->
      <router-view />
    <v-overlay :model-value="general_loading" persistent="true" scroll-strategy="block"
      class="align-center justify-center">
      <v-progress-circular color="primary" indeterminate size="100" :width="5">
        <template v-slot:default> Cargando...
        </template>
      </v-progress-circular>
    </v-overlay>
    <default-footer />
  </v-app>
</template>

<script>
import DefaultBar from './AppBar.vue'
import PrincipalView from './PrincipalView.vue'
import DefaultFooter from './Footer.vue'
import TerminosCondiciones from '@/components/TerminosCondiciones.vue'
import MenuBarNav from './MenuBarNav.vue'
import { mapState, mapActions } from "vuex";
export default {
  name: 'MainView',
  components: { DefaultFooter, DefaultBar, TerminosCondiciones, MenuBarNav, PrincipalView },
  computed: {
    ...mapState("layout", ['general_loading']),
  },
  data() {
    return {
      show_alert: true,
      visible: false,
      show_hidde_menu_nav: false,
      home_page_initial: false,
    }
  },
  mounted() {
    this.showGeneralLoading(true);
    this.navigateTo('home')
    this.showGeneralLoading(false);

  },
  methods: {
    ...mapActions("layout", ["showGeneralLoading"]),
    showLogin() {
      this.visible = !this.visible;
    },
    showHiddeMenu() {
      this.show_hidde_menu_nav = !this.show_hidde_menu_nav;
    },
    ActionInTyC(params) {
      this.show_terminos_condiciones = false;
    }
  }
}
</script>
